'use strict';

/*
* Custom Theme JS
*/
var Grav = require('../lib/gravitate/gravtheme/js/grav-modules.js')(jQuery);
var Swiper = require('swiper').default;

console.log(Swiper);

jQuery(function ($) {
	/**
  * Place items in here to have them run after the Dom is ready
  */
	$(document).ready(function () {
		Grav.filterLinks();
		Grav.setHeightVars();
		Grav.setScrollVars();
		Grav.addDropDownsToSubMenus();
		Grav.addListenerToMobileMenuButton();
		Grav.wrapInputItems();

		Grav.updateScrollClasses(100);

		$('.site-header__button-mobile-menu').on('click tap', function () {
			$('html').toggleClass('mobile-menu-active');
		});

		var swiperTest = new Swiper('.swiper-container');
	});

	/**
  * Place items in here to have them run the page is loaded
  */
	$(window).load(function () {});

	/**
  * Place items in here to have them run when the window is scrolled
  */
	$(window).scroll(function () {
		Grav.setScrollVars();
		Grav.updateScrollClasses(100);
	});

	/**
  * Place items in here to have them run when the window is resized
  */
	$(window).resize(function () {});
});


